:root {
  --site-width: 60rem;
  --main-color: #3f51b5;
}

body {
  background-color: #ececf77d !important;
  color: #444;
}

* {
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
}

/* Suppress react-router-dom <a> styles */
.router-link {
  text-decoration: inherit;
  color: inherit;
}

.site-width {
  max-width: 60rem;
  max-width: var(--site-width);
  margin: 0 auto;
}

.main-section {
  margin-top: 2rem;
  margin-bottom: 5rem;
}

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#nav {
  background-color: var(--main-color); }
  #nav img {
    width: 8rem; }

#home section {
  padding: 5rem 1rem; }

#home section:nth-child(even) {
  background-color: white; }

#home .title {
  font-size: 3rem;
  line-height: 58px;
  margin-bottom: 1rem; }

#home .sub-title {
  font-size: 1.5rem;
  line-height: 58px;
  margin-bottom: 1rem; }

#home p {
  font-size: 1.15rem;
  color: #848484; }

.recent-forms {
  margin-top: 2rem; }

.user-forms {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(11em, 1fr));
  grid-gap: 1.5rem; }

.form-preview {
  padding: 1rem;
  display: flex;
  flex-direction: column; }
  .form-preview .miniature {
    font-size: 3rem;
    align-self: center;
    margin-bottom: 1rem;
    color: #3f51b5;
    opacity: 0.6;
    -webkit-transition: opacity 0.2s ease;
    transition: opacity 0.2s ease; }
  .form-preview .formTitle {
    font-weight: bold; }
  .form-preview .formTitle,
  .form-preview .time {
    white-space: nowrap;
    overflow: hidden; }
  .form-preview .bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 1rem;
    color: gray;
    font-size: 0.8rem; }
    .form-preview .bottom svg {
      font-size: 1.3rem; }
  .form-preview:hover .miniature {
    opacity: 0.8; }
  .form-preview:hover .bottom svg {
    color: #3f51b5; }

:root {
  --default-spacing: 1rem;
  --horizontal-spacing: 0.5rem; }

.paper-section {
  padding: 2rem;
  margin: 1rem 0; }

button .button-svg {
  margin-right: 0.25rem; }

.option-row-input > div::before {
  border-bottom-color: transparent; }

.option-row > *:not(:last-child),
.question > .header > *:not(:last-child) {
  margin-right: 0.5rem;
  margin-right: var(--horizontal-spacing); }

.question svg:not(.circle),
.option-row:hover > .arrows > svg {
  opacity: 0.4;
  -webkit-transition: opacity 0.2s ease;
  transition: opacity 0.2s ease;
  cursor: pointer; }

.question svg:not(.circle):hover,
.option-row > .arrows > svg:hover {
  opacity: 1; }

.question svg.circle {
  opacity: 0.4; }

.question > div:not(:last-child) {
  margin-bottom: 1rem;
  margin-bottom: var(--default-spacing); }

.question > .header {
  display: flex;
  align-items: center; }
  .question > .header *:not(:last-child) {
    margin-right: 0.5rem;
    margin-right: var(--horizontal-spacing); }

.option-row {
  display: flex;
  align-items: center;
  height: 2rem; }
  .option-row:not(:last-child) {
    margin-bottom: 0.75rem; }
  .option-row > *:not(:last-child) {
    margin-right: 0.5rem;
    margin-right: var(--horizontal-spacing); }
  .option-row > .arrows > svg {
    opacity: 0.1; }
  .option-row > .text {
    flex: auto; }
  .option-row > .arrows {
    width: 3rem; }
  .option-row > .delete {
    width: 1.5rem; }
  .option-row.add-option {
    opacity: 0.5;
    /* Add option pseudo button */ }
    .option-row.add-option:hover {
      opacity: 0.7; }
    .option-row.add-option > .text:hover {
      cursor: pointer;
      opacity: 1; }
    .option-row.add-option button {
      text-transform: none;
      padding: 0;
      background-color: transparent; }

.add-question {
  display: flex;
  align-items: center;
  cursor: pointer;
  opacity: 0.4;
  -webkit-transition: opacity 0.2s ease;
  transition: opacity 0.2s ease; }
  .add-question:hover {
    opacity: 1; }
  .add-question svg {
    margin-right: 0.5rem; }

.footer-buttons {
  display: flex;
  justify-content: space-between; }
  .footer-buttons button {
    height: 2.3rem; }

.publish-button {
  margin-top: 3rem; }

.submit-mechanism {
  margin-top: 3rem; }
  .submit-mechanism .title {
    font-size: 2rem;
    color: #444; }
  .submit-mechanism p {
    color: #4d4d4d; }
  .submit-mechanism .alert {
    border-left: 0.4rem solid #fb8383;
    padding-left: 1rem; }
  .submit-mechanism .select {
    margin-right: 1rem;
    width: 9rem; }
  .submit-mechanism > button {
    margin-top: 1rem; }

.at-address {
  font-size: 0.8rem;
  opacity: 0.7; }
  .at-address a {
    color: var(--main-color);
    text-decoration: none; }

#sign-in {
  padding: 1rem; }
  #sign-in .title {
    text-align: center; }
  #sign-in .content {
    display: flex;
    flex-direction: column;
    place-items: center; }
  #sign-in .content > *:not(:last-child) {
    margin-bottom: 1rem; }
  #sign-in .logo {
    width: 10rem; }
  #sign-in .error {
    text-align: center;
    margin-top: 0.2rem;
    font-size: 0.8rem;
    color: #616161; }

.loading {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .loading .text {
    opacity: 0.7;
    font-size: 1rem;
    margin-bottom: 1rem; }

#form {
  color: #2e2e2e;
  max-width: 40rem;
  margin: 0 auto; }
  #form > *:not(:last-child) {
    margin-bottom: 3rem; }
  #form .header .title {
    font-size: 2.5rem; }
  #form .header .description {
    color: #444; }
  #form .stats {
    display: grid;
    grid-template-columns: repeat(2, -webkit-max-content);
    grid-template-columns: repeat(2, max-content);
    grid-gap: 0 1rem; }
    #form .stats > .name {
      font-size: 80%;
      opacity: 0.7; }
    #form .stats > .stat {
      font-size: 300%; }
  #form .form-question {
    --smaller-font: 0.875rem; }
    #form .form-question .text {
      font-size: 1.25rem;
      margin-bottom: 1rem; }
    #form .form-question .answers {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: var(--smaller-font) 1rem;
      font-size: var(--smaller-font); }
      #form .form-question .answers .option {
        display: flex;
        align-items: center; }
        #form .form-question .answers .option svg {
          opacity: 0.4;
          margin-right: var(--smaller-font); }
      #form .form-question .answers .progress .progress-bar {
        background-color: #3f51b5;
        color: white;
        height: 100%;
        border-radius: 0.25rem;
        display: flex;
        place-content: center;
        place-items: center; }
  #form .form-option {
    margin-bottom: 0; }
    #form .form-option > span {
      padding-top: 0;
      padding-bottom: 0; }
    #form .form-option:not(:last-child) {
      padding-bottom: 1rem; }

.address-list {
  font-size: 80%;
  opacity: 0.2;
  margin-bottom: 1.5em;
  -webkit-transition: opacity 0.4s ease;
  transition: opacity 0.4s ease; }

.address-list:hover {
  opacity: 0.7; }

